<template>
<div v-loading.fullscreen="loading" class="content">

    <div class="pbox">
        <img class="pagopa_icon" src="../assets/ether2.png" />
        <div class="pagopa-info">
            <p class="pagopa-label">等级: V{{ level }}</p>

            <p v-if="levelCount > 0" class="rate-label">有效金额: {{ levelCount }}ETH</p>
            <p v-else class="rate-label"> 立即参与赚取收益 </p>
        </div>

        <div class="raise-info">

            <b class="balance-big"> {{ withdrawEarned }}</b>
            <div>
                <span class="eth-label">共提取</span>
            </div>
        </div>
    </div>

    <div class="lan">
        <el-link :href="enUrl" >En</el-link>
    </div>

    <div class="open-info">
        <p>参与<label class="day gold-font "> {{ invest }} </label> eth, 享受<b>{{ earnP }}</b>%收益</p>
    </div>

    <div>
        <el-slider v-model="value" :show-tooltip="false" :format-tooltip="formatTooltip" :min="2" :max="150"></el-slider>
    </div>

    <div class="jbox">
        <el-button class="join" size="big" round @click="join">
            立即参与
        </el-button>
    </div>

    <div class="title">
        <img src="../assets/b_icon.svg" />
        <span>收益</span>
        <el-link size="small" type="primary" href="#/zh/order">参与记录> </el-link>
    </div>

    <div class="card-bkg mine-box ">
        <div class="award-des">
            <span> 可提取 </span>
            <b> {{ canWdEth }} ETH </b>
        </div>
        <el-button size="small" type="primary" @click="withdraw">提取</el-button>
    </div>

    <div class="title">
        <img src="../assets/pool.svg" />
        <span>伙伴</span>
    </div>

    <div class="card-bkg mine-box">
        <div class="award-des">
            <span> 节点数 </span>
            <b> {{ parnerNum }} 个 </b>
        </div>
        <el-link type="primary" href="#/zh/node">查看 > </el-link>
    </div>

    <div class="card-bkg qrcode-box" @click="copyUlink">
        <p class="qrcode-title">我的推广二维码</p>
        <p class="tips-font">点击二维码可复制推广链接</p>
        <qrcode :value="uLink" :options="{margin: 1, width: 120 }"></qrcode>
        <!-- <el-link type="primary" :href="uLink"> {{ uLink }} </el-link> -->
    </div>

</div>
</template>

<script>
/* eslint-disable */
import formatNum from "./../numcomp";
import MyVid from "./../my_vid.js";
import {
    Message,
    MessageBox
} from 'element-ui';

export default {
    name: 'Fov',
    data() {
        return {
            web3: null,
            fov: null,
            value: 31,
            invest: 2,
            enUrl: null,
            earnP: 3,
            level: null,
            levelCount: null,
            withdrawEarned: null,
            parnerNum: 0,
            canWdEth: 0,
            uLink: null,
            loading: false,
        }
    },

    created() {
        let addr = this.$route.params.p;

        if (addr && addr.length === 42) {
            this.parentAddr = addr;
            this.enUrl = "#/en/p/" + this.parentAddr
        } else {
            console.log("no parent");
        }

        MyVid.initWeb3Account((web3, acc) => {
            console.log("my account address:" + acc)
            this.account = acc
            this.uLink = process.env.VUE_APP_HOST + 'zh/p/' + this.account
            this.web3 = web3
            this.init()

        });
    },
    methods: {

        fn(num) {
            return formatNum(num, 2)
        },

        fnd(num, d) {
            return formatNum(num, d)
        },

        formatTooltip(val) {
            this.invest = (val / 10).toFixed(1);
            if (val <= 30) {
                this.earnP = 3;
            } else if (val <= 100) {
                this.earnP = 10;
            } else {
                this.earnP = 25;
            }

            return val / 10;
        },

        async init() {
            this.fov = await MyVid.getFov()
            console.log(this.fov.address);
            this.loading = false;

            this.info()
            this.getParner()
        },

        info() {
            this.fov.investInfo(this.account, {
                from: this.account
            }).then(
                r => {
                    this.level = r[0].toNumber();
                    this.levelCount = this.web3.utils.fromWei(r[1])
                }
            )

            this.fov.withdrawEarned(this.account, {
                from: this.account
            }).then(
                r => {
                    this.withdrawEarned = this.fn(this.web3.utils.fromWei(r), 4)
                }
            )

            this.fov.canWd({
                from: this.account
            }).then(r => {
                this.canWdEth = this.fnd(this.web3.utils.fromWei(r), 4);
            })
        },

        getParner() {
            this.fov.allChildNum(this.account, {
                from: this.account
            }).then(r => {
                this.parnerNum = r.toNumber();
                console.log(this.parnerNum)
            })

        },

        copyUlink() {
            if (!navigator.clipboard) {
                var textArea = document.createElement("textarea");
                textArea.value = this.uLink;
                textArea.style.position = "fixed"; //avoid scrolling to bottom
                document.body.appendChild(textArea);
                textArea.focus();
                textArea.select();

                try {
                    var successful = document.execCommand('copy');
                    var msg = successful ? 'successful' : 'unsuccessful';
                    console.log('Fallback: Copying text command was ' + msg);

                    Message.info({
                        "message": "链接已复制"
                    })

                } catch (err) {
                }

                document.body.removeChild(textArea);
            } else {
                navigator.clipboard.writeText(this.uLink).then(function () {
                    Message.info({
                        "message": "推广链接已复制"
                    })
                }, function (err) {
                    Message.info({
                        "message": "推广链接复制"
                    })
                });
            }

        },

        withdraw() {
            this.fov.withdraw({
                from: this.account
            }).then(
                r => {
                    this.info()
                }
            )
        },

        join() {

            if (!this.parentAddr) {

                MessageBox.alert("无效的节点! ", "提示", {
                    confirmButtonText: '确定'
                })

                return;
            } else {
                this.fov.join(this.parentAddr, {
                    from: this.account,
                    value: this.web3.utils.toWei(this.invest, 'ether')
                }).then(r => {
                    this.info()
                })
            }
        },

    }
}
</script>

