<template>
<div v-loading.fullscreen="loading" class="content content-on-tab">

    <div class="header">
        <span class="el-icon-back back" @click="goBack"> </span>
        <div class="header-title"> Orders </div>
        <span class="back" @click="goBack"> </span>
    </div>

    <div class="title">
        <img src="../assets/wallet.svg" />
        <span>Account</span>
    </div>
    <div class="balance-content">
        <div>
            <div class="icon-content">
                <span class="gold-font">Level</span>
            </div>
            <p class="gold-font">V{{ level }} </p>
        </div>
        <div>
            <div class="icon-content">
                <span class="gold-font">Balance</span>
            </div>
            <p class="gold-font">{{ balance }} </p>
        </div>
    </div>

    <div class="card"  v-bind:key="index" v-for="(order, index) in orders">
        <div class="box_top"><span>Join: {{order.date }}</span>  <el-tag :type=order.tagtype > {{order.wd }} </el-tag> </div>
        <div class="box_top"><span>Contract Period: {{order.period }} days</span>  </div>
        <div class="card-divider"></div>
        <div class="box_content"><span>Amout：<b>{{ order.value }} ETH</b> </span><span>Level：<b>V{{order.level }}</b> </span></div>
        <div class="box_content"><span>Earn：<b>{{ order.percent }} %</b> </span><span>Value：<b>{{order.earn }}ETH</b> </span></div>
        
    </div>

    <p class="tips-font ">Directions:<br />
        1. Level depending on the invested ether. <br />
        2. Higher the level, then time will longer and earn more. <br />
    </p>

</div>
</template>

<script>
/* eslint-disable */
import MyVid from "./../my_vid.js";
import formatNum from "./../numcomp";
import {
    MessageBox
} from 'element-ui';

export default {
    name: 'OrderEN',
    data() {
        return {
            web3: null,
            level: null,
            balance: null,
            levelCount: null,
            loading: true,
            orders: [],
            orderNums: null,
        }
    },

    created() {

        MyVid.initWeb3Account((web3, acc) => {
            console.log("account:" + acc)
            this.account = acc
            this.web3 = web3
            this.init()

        });
    },
    methods: {

        fn(num) {
            return formatNum(num, 2)
        },

        goBack() {
            this.$router.back(-1)
        },

        async init() {
            this.fov = await MyVid.getFov()
            console.log(this.fov.address);

            this.loading = false;

            this.info()
            this.myOrders()

        },

        info() {
            this.fov.investInfo(this.account, {
                from: this.account
            }).then(
                r => {
                    this.level = r[0].toNumber();
                    this.levelCount = this.web3.utils.fromWei(r[1])
                }
            )

            this.web3.eth.getBalance(this.account).then(r => {
                this.balance = this.fn(this.web3.utils.fromWei(r), 4)
            })
        },

        myOrders() {
            this.orders = []
            this.fov.orderNum({
                from: this.account
            }).then(r => {
                this.orderNums = r.toNumber()
                if (this.orderNums > 0) {
                    this.getOrder(this.orderNums - 1)
                }
                this.$forceUpdate();
            })
        },

        getOrder(index) {
            this.fov.order(index, {
                from: this.account
            }).then(r => {
                var joinTs = r[1] * 1000;
                var joinTime = new Date(joinTs)
                var dateStr = joinTime.toLocaleDateString().replace(/\//g, "-") + " " + joinTime.toTimeString().substr(0, 8);
                var level = 0;
                var statusStr = "Earning"
                var type = "success"
                var percent = 3
                var earn = 0
                var period

                var eth = parseFloat(this.web3.utils.fromWei(r[0]))

                var currTime = new Date().getTime();

                if (eth < 3.1) {
                    level = 1;
                    percent = 3
                    period = 3;
                    earn = this.fn(eth + (eth * 3 / 100), 3);
                    var joinEnd = joinTs + 3 * 1000 * 60 * 60 * 24;
                    if (currTime > joinEnd) {
                        statusStr = "Passed"
                        type = "info"
                    }

                } else if (eth >= 3.1 && eth < 10.1) {
                    level = 2;
                    percent = 10;
                    earn = eth + (eth * 10 / 100);
                    period = 7;
                    var joinEnd = joinTs + 7 * 1000 * 60 * 60 * 24;
                    if (currTime > joinEnd) {
                        statusStr = "Passed"
                        type = "info"
                    }
                } else {
                    level = 3;
                    percent = 25;
                    earn = eth + (eth * 25 / 100)
                    period = 15;
                    var joinEnd = joinTs + 15 * 1000 * 60 //  * 60 * 24;
                    if (currTime > joinEnd) {
                        statusStr = "Passed"
                        type = "info"
                    }
                }

                if (r[2]) {
                    statusStr = "Extracted"
                    type = "success"
                }
                let order = {
                    value: this.web3.utils.fromWei(r[0]),
                    date: dateStr,
                    wd: statusStr,
                    level: level,
                    percent: percent,
                    period: period,
                    earn: earn,
                    tagtype: type
                }

                this.orders.push(order);

                if (index > 0) {
                    this.getOrder(index - 1)
                }
                this.$forceUpdate();
            });
        }

    }
}
</script>

