<template>
<div v-loading.fullscreen="loading">
    <div class="header">
        <span class="el-icon-back back" @click="goBack"> </span>
        <div class="header-title"> My Partners </div>
        <span class="back" @click="goBack"> </span>
    </div>

    <div class="table-content">

        <el-table :data="nodes" align="center" stripe style="width: 100%">
            <el-table-column prop="addr" label="Addr." align="left" width="105">
            </el-table-column>
            <el-table-column prop="level" label="Lv." align="center" width="50">
            </el-table-column>
            <el-table-column prop="joined" label="Ethers" align="center" width="110">
            </el-table-column>

            <el-table-column prop="num" align="center" label="Nodes">
            </el-table-column>
        </el-table>

        <div class="loading_more" v-loading="loading_more" element-loading-text="loading" element-loading-spinner="el-icon-loading">
            <label> &nbsp; </label>
            <el-button v-if="!loading_more && parnerNum != nodes.length " @click="loadMore">Load More</el-button>

        </div>
    </div>

</div>
</template>

<script>
/* eslint-disable */
import MyVid from "./../my_vid.js";
import formatNum from "./../numcomp";
import lang from 'element-ui/lib/locale/lang/en'
import locale from 'element-ui/lib/locale'
locale.use(lang)




export default {
    name: 'Node',
    data() {
        return {
            lastAwardEth: "~",
            round: 0,
            title: null,
            loading_more: false,
            nodes: [],
            parnerNum: 0,
            currLoadNodes:[],
            nextLoadingNodes: [],
            loading: false
        }
    },

    created() {
        MyVid.initWeb3Account((web3, acc) => {
            this.account = acc
            this.web3 = web3
            this.init()
        });

    },
    methods: {

        goBack() {
            this.$router.back(-1)
        },

        async init() {
            this.fov = await MyVid.getFov()
            this.loading = false
            this.currLoadNodes.push(this.account);
            this.getParner()
            this.loadChilds(this.account)
        },

        fn(num) {
            return formatNum(num, 4)
        },

        getParner() {
            this.fov.allChildNum(this.account, {
                from: this.account
            }).then(r => {
                this.parnerNum = r.toNumber();
                console.log(this.parnerNum)
            })

        },

        async loadChilds(node) {
            let arr = await this.fov.myChilds({
                from: node
            });
            
            for (let i = 0; i < arr.length; i++) {
                this.nextLoadingNodes.push(arr[i]); // 下一次加载的节点
                await this.loadNodeInfo(arr[i]);
            }

            if (node == this.currLoadNodes[this.currLoadNodes.length-1] ) {
              this.loading_more = false
              if (this.parnerNum>  this.nodes.length && this.nodes.length < 10 ) {
                this.loadMore()
              }
            }

        },

        async loadNodeInfo(node) {

            var info = await this.fov.investInfo(node, {
                from: this.account
            })
            var childNum = await this.fov.childNum(node, {
                from: this.account
            })
            // var joined = await this.fov.joined(node, {
            //     from: this.account
            // })

            var nodeInfo = {
                addr: node.substring(0, 9) + "..",
                level: info[0].toNumber(),
                joined: this.web3.utils.fromWei(info[2]),
                num: childNum.toNumber()
            }

            this.nodes.push(nodeInfo)
            this.$forceUpdate();
        },

        loadMore() {
            this.loading_more = true

            console.log("load more ");

            this.currLoadNodes = this.nextLoadingNodes.slice();
            this.nextLoadingNodes = [];

            for (let i = 0; i < this.currLoadNodes.length; i++) {
                this.loadChilds(this.currLoadNodes[i]);
            }

        }

    },

}
</script>

<style scoped>
.loading_more {
    padding: 10px;
}

.table-content {
    padding: 10px;
}
</style>
