import Vue from 'vue'
import Router from 'vue-router'
import Fov from './components/Fov.vue'
import FovEN from './components/FovEN.vue'
import Admin from './components/Admin.vue'
import Order from './components/Order.vue'
import OrderEN from './components/OrderEN.vue'
import Node from './components/Node.vue'
import NodeEN from './components/NodeEN.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/p',
      name: 'fov',
      component: Fov
    },
    {
      path: '/en/p',
      name: 'fov',
      component: FovEN
    },
    {
      path: '/en/p/:p',
      name: 'foven',
      component: FovEN
    },
    {
      path: '/zh/p/:p',
      name: 'fov',
      component: Fov
    },
    {
      path: '/zh/order',
      name: 'order',
      component: Order
    },
    {
      path: '/en/order',
      name: 'orderen',
      component: OrderEN
    },
    {
      path: '/admin',
      name: 'admin',
      component: Admin
    },
    {
      path: '/zh/node',
      name: 'node',
      component: Node
    }
    ,
    {
      path: '/en/node',
      name: 'node',
      component: NodeEN
    }

  ]
  }
)

